/* Chat Box */

.app {
	background-color: #f0f0f0;
	width: 900px;
	height: 450px;
  box-shadow: 0px 8px 60px -10px white; 
	border-radius: 6px;
	overflow: hidden;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
  margin: auto;
  position: relative;
}

.app .contact-list {
	background-color: #f0f0f0;
	width: 200px;
	overflow: auto;
}

.app .contact-list .title {
	padding: 12px;
	margin: 0;
	padding-bottom: 0;
	font-size: 150%;
}

.app .contact-list ul {
	list-style: none;
	margin: 0;
	padding: 12px;
}

.app .contact-list ul li {
	margin: 8px 0;
	padding: 8px 12px;
	position: relative;
	cursor: pointer;
	transition: all .2s ease-out;
}

.app .contact-list ul li.active, .app .contact-list ul li:hover {
	background-color: #e1f0fa;
	color: #3498db;
	border-radius: 6px;
}

.app .messages {
	margin: 12px;
	background-color: #D9D7DD;
	border-radius: 6px;
	overflow: hidden;
	-webkit-flex: 1 1 auto;
	        flex: 1 1 auto;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
  position: absolute;

  top: 0;
  bottom: 0;
  right: 10px;
  left: 200px;
}

.app .messages .messages-history {
	overflow: auto;
	-webkit-flex: 1 1 auto;
	        flex: 1 1 auto;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column-reverse;
	        flex-direction: column-reverse;
	-webkit-align-items: end;
	        align-items: end;
}

.app .messages .messages-history .message {
	width: 100%;
	padding: 12px;
	padding-top: 0;
}

.app .messages .messages-history .message:last-child {
	padding-top: 12px;
}

.app .messages .messages-history .message.me {
	text-align: right;
}

.app .messages .messages-history .message.me .message-body {
	color: white;
	background-color: #3498db;
}

.app .messages .messages-history .message .message-body {
	background-color: #D9D7DD;
	color: #333;
	font-weight: 400;
	display: inline-block;
	padding: 8px 12px;
	border-radius: 6px;
	max-width: 400px;
	min-width: 75px;
	line-height: 20px;
	text-align: left;
	text-align: initial;
}

.app .messages .messages-inputs {
	display: -webkit-flex;
	display: flex;
  border-top: 1px solid #ccc;
}

.app .messages .messages-inputs input[type="text"] {
	background-color: transparent;
	border: none;
	padding: 12px;
	resize: vertical;
	-webkit-flex: 1 1 auto;
	        flex: 1 1 auto;
}

.app .messages .messages-inputs button {
	padding: 0 12px;
	background-color: transparent;
	border: none;
	cursor: pointer;
}

.app .messages .messages-inputs button:hover i {
	color: #3498db;
}

.app .messages .messages-inputs button i {
	vertical-align: middle;
	transition: all .2s ease-out;
}
